:root {
  --rad: 10px;
  --font-fam: "Lato", sans-serif;
  --height: 50px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-top: 30px;
  display: flex;
}

.info {
  justify-content: space-evenly;
  display: flex;
}

.span {
  text-align: center;
  color: #000;
  font-weight: 300;
  display: block;
}

.header {
  height: 80px;
  background-color: #244b8f;
}

.search-form {
  width: 30rem;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  display: flex;
}

input, .searchBtn {
  height: var(--height);
  font-family: var(--font-fam);
  border: 0;
  font-size: 20px;
}

.searchBtn {
  color: #fff;
  border-radius: 0 10px 10px 0;
  position: relative;
  right: 50px;
}

.searchBtn:hover {
  background-color: #e4c2c2;
}

input[type="text"] {
  width: 100%;
  border-radius: var(--rad);
  outline: 0;
  padding: 0 10px;
}

input {
  width: calc(100% - var(--btn-width) );
}

.sl-counter {
  display: none;
}

.sl-current {
  visibility: hidden;
}

.sl-total {
  display: none;
}

.load-more {
  box-shadow: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: var(--font-fam);
  height: 50px;
  background-color: #244b8f;
  border-style: none;
  border-radius: 10px;
  margin-left: 50%;
  padding: 14px 40px;
  font-size: 17px;
  font-weight: 900;
  transition: all .3s;
  transform: translate3d(0, 0, 0);
}

.load-more:hover {
  opacity: 1;
  background-color: #2f62bc;
  transition-duration: .35s;
  transform: translateY(0);
  box-shadow: 0 5px 30px rgba(0, 0, 0, .05), 0 1px 4px rgba(0, 0, 0, .05);
}

.load-more:hover:after {
  opacity: .5;
}

.load-more:active {
  transition-duration: .35s;
  transform: translateY(2px);
  box-shadow: 0 3px 6px rgba(0, 0, 0, .1), 0 0 10px rgba(0, 0, 0, .1), 0 1px 4px -1px rgba(0, 0, 0, .1);
}

.load-more:active:after {
  opacity: 1;
}

